/* eslint-disable no-undef */
$(function() {
    // 移除sidebar条件判断，或者确认页面上有.sidebar元素
    // if ($('.sidebar').length > 0) {
        toggleTool();
        var move;
        var flag = true;
        
        // 点击事件 - 楼层跳转
        $(".guding li").on('click', function () {
            flag = false;
            console.log("点击了第" + $(this).index() + "个楼层按钮");
            move = $(".floor>div").eq($(this).index()).offset().top;
            $("html,body").stop().animate({
                scrollTop: move
            }, function () {
                flag = true;
            });
            $(this).addClass("bgce").siblings().removeClass("bgce");
            $(this).children("a").css("color", "#ffffff").parent().siblings().children("a").css("color", "#666666");
        });
        
        function toggleTool() {
            // 确保.like元素存在
            if ($(".like").length > 0) {
                var likeMove = $(".like").offset().top;
                if ($(document).scrollTop() >= likeMove) {
                    $(".guding").fadeIn();
                } else {
                    $(".guding").fadeOut();
                }
            }
        }
        
        // 页面滚动事件
        $(document).on('scroll', function () {
            toggleTool();
            if (flag) {
                $(".floor>div").each(function (i, ele) {
                    if ($(document).scrollTop() >= $(ele).offset().top) {
                        $(".guding li").eq(i).addClass("bgce").siblings().removeClass("bgce");
                        $(".guding li").eq(i).children("a").css("color", "#ffffff").parent().siblings().children("a").css("color", "#666666");
                    }
                });
            }
        });
    // }
});